export const getCalculatorData = (calculator) => {
  const selectedCal = calculators[calculator];

  if (!selectedCal) return [];

  return selectedCal;
}

export const getDefaultProduct = ()=>{
  return {name:(Object.keys(calculators))[0], details: calculators[(Object.keys(calculators))[0]]};
}

export const getSelectedProductPath =(productName)=>{
  for(const propertyName in calculators){
    if(propertyName === productName)
      return calculators[propertyName].path;
  }

}
export const isCalculator = (productName)=>{
  return (Object.keys(calculators)).includes(productName);
}

export const getCalculators = () => {

  return Object.keys(calculators).map(key => { return { title: calculators[key].title, keyName: key } });
}

export const getSelectedProductDetails =(name) =>{
  for(const propertyName in calculators){
    if(calculators[propertyName].path === name)
      return {name:propertyName, details:calculators[propertyName]}
  }

  return null;
}