import { data } from "../Chart";

const N=12;

export function sipTotalInvestment(data) {
  return BAFWPV(data)/((((1+(data.R/100)/12)**(12*(data.t)))-1)/((data.R/100)/12));
}

export function lumpsumTotalInvestment(data) {
  return data.pv;
}

export function sipTotalReturns(data) {
  return data.pv*((1+(data.r/100/12))**(data.t*12)-1)/(data.r/100/12);
}

export function sipReturns(data) {
  return sipTotalReturns(data) - mSIPTotalInvestment(data) ;
}

export function lumpsumReturns(data) {
  return lumpsumTotalReturns(data) - data.pv;
}

export function lumpsumTotalReturns(data) {
  return data.pv*((1+(data.r/100))**(data.t));
}

export function inflationTotalValue(data){
  return data.pv*((1+(data.r/100))**data.t);
}

export function inflationReturn(data){
 return inflationTotalValue(data) - data.pv;
}

export function inflationInvestmentValue(data){
  return data.pv;
}

/**Become a financially independent women */
export function BAFWFV(data){
  return data.pv*((1+(data.r/100))**data.t);
}

/**Become a financially independent women */
export function BAFWPV(data){
  return BAFWFV(data) *(1-1/(1+(data.R/100))**data.n)/(data.R/100);
}
/** marriage planner */

function getZ(data){
  return data.y-data.x;
}
export function marriageCostInFuture(data){
 return data.pv*(((1+data.inflation))**getZ(data));
}

export function lumpsumNeeded(data){
  return marriageCostInFuture(data)/(((1+data.r/100))**getZ(data));
}

export function monthlyInvestment(data){
  return marriageCostInFuture(data)/(((1+data.r/data.n)**data.n*data.z-1)/data.r/data.n);
}

export function monthlyInvestmentIfLumpsum(data){
  return (data.n*marriageCostInFuture(data))/(((1+data.r/data.n)**data.n*data.z-1)/data.r/data.n);
}

/**Goal Base retirement bgi */
export function gBIinflationAdjustedRate(data){
  return ((((1+data.r/100)/(1+data.inflation/100))-1)*100);
}

function adjestedReturn(data){
 return data.r-data.inflation;
}

function gBNFV(data){
  return data.fv-(data.l*((1+(data.r/100))**data.z));

  //K=L*(1+R)^Z
}

export function gBLumpsumInvestment(data){
  return data.fv/((1+(gBIinflationAdjustedRate(data)/100))**data.z);
}

export function gBImonthlyInvestment(data){
  if(data.l>0)
    return gBNFV(data)/((((1+((gBIinflationAdjustedRate(data)/100)/N))**(N*data.z))-1)/((gBIinflationAdjustedRate(data)/100)/N));
  return data.fv/((((1+((gBIinflationAdjustedRate(data)/100)/N))**(N*data.z))-1)/((gBIinflationAdjustedRate(data)/100)/N));

  //PMT=FV/(((1+R/n)^n*z-1)/R/n)
}

export function gBIpresentAmount(data){
  return (data.n*marriageCostInFuture(data))/((((1+data.r/data.n)**data.n*data.z)-1)/data.r/data.n);
}

/**Time Value of money tvm */
export function tVMFutureValueWorth(data){
  const n=data.t;
  return data.fv/((1+(data.r/100))**(n));
}

export function tVMReturns(data){
  return data.fv - tVMFutureValueWorth(data);
  //return marriageCostInFuture(data)/(((1+data.r/data.n)**data.n*data.z-1)/data.r/data.n);
}

/**Vacation abroad vB */
export function vBCostFuture(data){
  return data.pv*((1+(data.inflation/100))**data.z);
}

export function  vBInvestLumpsum(data){
  return vBCostFuture(data)/((1+(data.r/100))**data.z);
}

function vBNFV(data){
  return vBCostFuture(data)-(data.l*(1+(data.r/100))**data.z);
}
export function  vBInvestMonthly(data){
  if(data.l>0)
    return vBNFV(data)/(((1+(data.r/100)/N)**(N*data.z-1))/(data.r/100)/N);

  return vBCostFuture(data)/(((1+(data.r/100)/N)**(N*data.z-1))/(data.r/100)/N);
}


export function  vBInvestMonthlyIfLumpsum(data){// might not needed.
  return (data.n*marriageCostInFuture(data))/(((1+data.r/data.n)**data.n*data.z-1)/data.r/data.n);
}

