/**Business Capital bC */
export function bcCostFuture(data){
  return marriageCostInFuture(data)/(((1+data.r/100))**getZ(data));
}

export function  bCInvestLumpsum(data){
  const r=((1+ data.r)/(1+data.inflation))-1;
  return data.fv/((1+(r/100))^N);
}

export function  bCInvestMonthly(data){
  const r=((1+ data.r)/(1+data.inflation))-1;
  return data.fv/(((1+(r/N))^N*data.z-1)/r/N);
}


export function  bCInvestMonthlyIfLumpsum(data){// might not needed.
  return (data.n*marriageCostInFuture(data))/(((1+data.r/data.n)**data.n*data.z-1)/data.r/data.n);
}


/**Plan wedding Expenses */
export function mcCostFuture(data){
  return marriageCostInFuture(data)*(((1+data.inflation/100))**getZ(data));
}

export function  mCInvestLumpsum(data){
  const n=12; //Ask the sridhar to logic behind the input n
  return marriageCostInFuture(data)/(((1+(data.r/100)/n)**(n*(getZ(data)-1)))/(data.r/100)/n);
}

function getNFV(data){
  return marriageCostInFuture(data)-(data.l*((1+data.r/100)**getZ(data)));
}

export function  mCInvestMonthly(data){
  const n=12; //Ask the sridhar to logic behind the input n
  if(data.l>0)
    return (getNFV(data))/(((1+(data.r/100)/n)**(n*(getZ(data)-1)))/(data.r/100)/n);
  else
    return (marriageCostInFuture(data))/(((1+(data.r/100)/n)**(n*(getZ(data)-1)))/(data.r/100)/n);
}

export function  mCInvestMonthlyIfLumpsum(data){// might not needed.
  const n=12; //Ask the sridhar to logic behind the input n
  return (n*marriageCostInFuture(data))/(((1+data.r/n)**(n*(getZ(data)-1)))/data.r/n);
}

// Become a Crorepati
export function becomeACorepatiTime(data){
  return  Math.log((((data.cr)*(inflationAdjustedReturn(data)/100))/(data.sip))+1)/ Math.log(1+(inflationAdjustedReturn(data)/100));
}

export function becomeACorepatiTimeinYears(data){
  return becomeACorepatiTime(data)/12;
}
export function inflationAdjustedReturn(data){
  return (((1+(data.r/100))/(1+(data.inflation/100)))-1)*100;
}

// Monthly SIP
export function mSIPTotalReturns(data) {
  const r=data.r/100/12;
  const n=data.t*12;
  return data.pv*((1+(r))**(n)-1)/(r);
}

export function mSIPReturns(data) {
  return mSIPTotalReturns(data) - mSIPTotalInvestment(data) ;
}

export function mSIPTotalInvestment(data) {
  return data.pv * data.t * 12;
}




